@import url("https://fonts.googleapis.com/css?family=Dosis|Open+Sans+Condensed:300|Roboto+Condensed|Sniglet");

.App {
  text-align: center;
  height: 100vh;
  font-family: "Dosis", sans-serif;
  background-color: rgb(255, 255, 255);
  /* background-image: url('../assets/background3.png'); */
  /* background: url('http://i.imgur.com/B0gV7Gq.png'); */
  /* background: url('https://wallup.net/wp-content/uploads/2016/05/25/176883-minimalism-geometry-hexagon-simple_background-monochrome-white_background-748x421.png'); */
  /* background-size: cover; */
  background-repeat: no-repeat;
  background-blend-mode: luminosity;
  background-size: 35%;
  background-position: left bottom;
}

.Section {
  height: 70%;
  padding: 3%;
  margin: 0;
  color: rgb(19, 10, 6);
  font-family: "Sniglet", cursive;
}
